<template>
  <div class="layout">
    <div class="layout-left" :class="{ 'layout-left-collapsed': collapsed }">
      <!-- 菜单头部 start -->
      <div class="head">
        <!-- <div class="head-icon" @click="toggleCollapsed">
          <a-icon :type="collapsed ? 'menu-unfold' : 'menu-fold'" />
        </div>
        <div class="head-line"></div> -->
      </div>
      <!-- 菜单头部 end -->

      <!-- 菜单 start -->
      <a-menu
        class="default-menu"
        mode="inline"
        v-model="selectedKeys"
        :openKeys="openKeys"
        :inline-collapsed="collapsed"
        @click="menuClick"
        @openChange="openChange"
      >
        <template v-for="item in menus">
          <a-sub-menu v-if="item.childrens.length > 0" :key="item.key">
            <span slot="title">
              <a-icon :type="item.icon" />
              <span>{{ item.title }}</span>
            </span>
            <a-menu-item v-for="children in item.childrens" :key="children.key">
              <router-link :to="children.url">
                <a-icon :type="children.icon" />
                <span>{{ children.title }}</span>
              </router-link>
            </a-menu-item>
          </a-sub-menu>
          <a-menu-item v-else :key="item.key">
            <router-link :to="item.url">
              <a-icon :type="item.icon" />
              <span>{{ item.title }}</span>
            </router-link>
          </a-menu-item>
        </template>
      </a-menu>
      <!-- 菜单 end -->
    </div>

    <!-- 右侧内容区域 start -->
    <div class="layout-right" :class="{ 'layout-right-collapsed': collapsed }">
      <slot></slot>
    </div>
    <!-- 右侧内容区域 end -->
  </div>
</template>
<script>
export default {
  name: "Layout",
  props: {
    /**
     * 菜单项数组 结构如下
     * {
     *     key: "", // 菜单唯一值
     *     title: "", // 菜单显示名称
     *     icon: "", // 菜单icon
     *     url: "", // 菜单路由 （当childrens里有类容时，可以为空）
     *     childrens: [] // 子项菜单组（里面类容和上面一样，但是不支持childrens）
     * }
     */
    menus: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      openKeys: [], // 展开的菜单项
      selectedKeys: [], // 想着的菜单项
      collapsed: false // 菜单是否收齐
    };
  },

  mounted() {
    let path = this.$route.path;
    let selectedKeys = [];
    let openKeys = [];
    let menus = this.menus;
    // 通过路由判断刷新加载选中菜单项
    for (const key in menus) {
      let item = menus[key];
      // 检查子项中是否存在路由匹配菜单项
      let childrenItem = item.childrens.find(children => {
        return children.url == path;
      });

      // 当存在是复制与选择值中
      if (childrenItem != null) {
        //childrenItem.key 菜单子项key, item.key 菜单一级key
        selectedKeys = [childrenItem.key, item.key];
        openKeys = [item.key]; // 展开该项菜单
        break;
      } else if (item.url == path) {
        // 当前路由在父层菜单中直接赋值选中项，不需要展开菜单项
        selectedKeys = [item.key];
        openKeys = []; // 展开菜单项置空
        break;
      }
    }

    this.selectedKeys = selectedKeys;
    this.openKeys = openKeys;
  },
  methods: {
    /**
     * 菜单折叠事件
     */
    toggleCollapsed() {
    //   this.collapsed = !this.collapsed;
    },

    /**
     * 点击菜单事件
     */
    menuClick({ item, key, keyPath }) {
      // this.selectedKeys = keyPath;
      console.log(item, key, keyPath);
    },

    /**
     * 展开菜单事件
     */
    openChange(openKeys) {
      console.log(openKeys);
      this.openKeys = openKeys;
    }
  }
};
</script>
<style lang="less">
@import "./index.less";
</style>
